* {
    font-family: Helvetica, sans-serif;

}

body {
    padding: 60px;
}

input {
    font-size: 1.2rem;
    width: 400px;
    color: rgb(156, 158, 172);
}

.card {
    text-align: center;
    border: 1px solid grey;
    border-radius: 3px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;

}

.card img {
    max-height: 170px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}